import { Injectable } from '@angular/core';
import {
  AngularFirestore,
  AngularFirestoreCollection,
} from '@angular/fire/firestore';
import { Router } from '@angular/router';
import firebase from 'firebase/app';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Order } from '../models/order';
import { User } from '../models/user';
import { AlertService } from './alert.service';

@Injectable({
  providedIn: 'root',
})
export class OrderService {
  orderCollectionRef: AngularFirestoreCollection<Order>;
  orders: Observable<Order[]>;

  constructor(
    private afs: AngularFirestore,
    private alert: AlertService,
    private router: Router
  ) {
    this.orderCollectionRef = this.afs.collection('orders');
    this.orders = this.orderCollectionRef.snapshotChanges().pipe(
      map((data) =>
        data.map((a) => {
          const data = a.payload.doc.data() as Order;
          const id = a.payload.doc.id;
          return { ...data, id };
        })
      )
    );
  }

  getAllOrdersId() {
    return this.orders;
  }

  getUserOrdersList(userId: string): Observable<User> {
    const userDoc = this.afs.doc(`users/${userId}`);
    const userData$ = userDoc.snapshotChanges().pipe(
      map((data) => {
        return data.payload.data() as User;
      })
    );
    return userData$;
  }

  placeOrder(order: any) {
    let currUser = JSON.parse(localStorage.getItem('user'));
    let userId = currUser.uid;
    this.afs
      .collection('orders')
      .add({
        order,
      })
      .then((response) => {
        this.afs
          .collection('users')
          .doc(userId)
          .update({
            orders: firebase.firestore.FieldValue.arrayUnion(response.id),
          });
      })
      .then(() => {
        this.router.navigate(['dashboard/client']);
        this.alert.success(' ');
      });
  }

  getOrderById(id): Observable<Order> {
    const orderDoc = this.afs.doc(`orders/${id}`);
    const orderData$ = orderDoc.snapshotChanges().pipe(
      map((data) => {
        return data.payload.data() as Order;
      })
    );
    return orderData$;
  }

  getOrdersByDriverUid(id) {
    const collectionRef = this.afs.collection('orders', (orders) =>
      orders.where('order.shipDetails.driver.driverUid', '==', id)
    );
    const resultDocuments$ = collectionRef.snapshotChanges().pipe(
      map((data) =>
        data.map((a) => {
          const id = a.payload.doc.id;
          return { ...(a.payload.doc.data() as Order), id };
        })
      )
    );
    return resultDocuments$;
  }

  changeOrderStatus(orderId, newStatus) {
    this.afs
      .collection('orders')
      .doc(orderId)
      .update({
        'order.status': newStatus,
      })
      .then(() => {
        this.alert.changeStatusInfo(orderId, newStatus);
      });
  }

  getOrdersByDate(date) {
    const orderCollectionRef = this.afs.collection('orders', (o) =>
      o.where('order.shipDetails.date', '==', date)
    );
    const resultData$ = orderCollectionRef.snapshotChanges().pipe(
      map((data) =>
        data.map((a) => {
          const data = a.payload.doc.data() as Order;
          const id = a.payload.doc.id;
          return { ...data, id };
        })
      )
    );
    return resultData$;
  }
}
