import { OrderService } from 'src/app/services/order.service';
import { Order } from 'src/app/models/order';
import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-accordeon-items',
  templateUrl: './accordeon-items.component.html',
  styleUrls: ['./accordeon-items.component.scss'],
})
export class AccordeonItemsComponent implements OnInit {
  @Input() orders: Order[];
  @Input() role: string;
  constructor(private orderService: OrderService) {}

  ngOnInit(): void {}
  changeStatus(id: string, status: string | number) {
    if (Number(status)) {
      let str = `   ${status}`;
      this.orderService.changeOrderStatus(id, str);
    } else {
      this.orderService.changeOrderStatus(id, status);
    }
  }
}
